@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --dark-blue: 238 29% 16%;
  --red: 14 88% 65%;
  --violet: 273 75% 66%;
  --blue: 240 73% 65%;
  --white: 0 0% 100%;
  --black: 0 0% 8%;
  --selection-bg: 277 100% 50%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Kumbh Sans", sans-serif;
}

body {
  background-image: linear-gradient(0deg, hsl(240deg, 73%, 65%) 63%, hsl(270deg, 75%, 69%) 100%, hsl(300deg, 76%, 72%) 100%);
  background-repeat: no-repeat;
  font-size: 0.75rem;
}

p:not([class]) {
  max-width: 45ch;
}

/**
 * Colors
 */
.bg-white {
  background-color: hsl(var(--white));
}

.clr-white {
  color: hsl(var(--white));
}

.bg-black {
  background-color: hsl(var(--black));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1440px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 0);
  margin-inline: auto;
}

/**
 * ### Flex 
 */
.flex {
  display: flex;
}

.flex-dir-col {
  flex-direction: column;
}

.justify-cont-betw {
  justify-content: space-between;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  align-content: space-around;
}
@media (min-width: 52.5em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Gap 
 */
.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.gap-3 {
  gap: 3rem;
}

/**
 * Float
 */
.float-l {
  float: left;
}

.float-r {
  float: right;
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Justify content center , start
 */
.justify-cont-center {
  justify-content: center;
}

.justify-cont-start {
  justify-content: start;
}

/**
 * ### Justify items center , start
 */
.justify-item-center {
  justify-items: center;
}

.justify-items-start {
  justify-items: start;
}

/**
 * ### Text  center 
 */
.text-align-center {
  text-align: center;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 3em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-500 {
  margin-block-end: 1.5rem;
}

/**
 * Padding top and bottom
 */
.padding-block-400 {
  padding-block: 1rem;
}

.padding-block-500 {
  padding-block: 1.5rem;
}

.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-500 {
  padding-inline: 1.5rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap; /* added line */
  border: 0;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 1rem 1.375rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100vw;
  height: 100%;
}

main {
  height: 27rem;
}
main::after {
  display: none;
}
@media (min-width: 62.5em) {
  main::after {
    display: block;
    width: 10rem;
    height: 6rem;
    content: "";
    position: relative;
    bottom: 16rem;
    left: -6.5rem;
    background-image: url("/images/illustration-box-desktop.svg");
    background-size: 10rem;
    background-repeat: no-repeat;
  }
}

.card {
  background-color: hsl(var(--white));
  border-radius: 1.5rem;
}
@media (min-width: 62.5em) {
  .card {
    display: flex;
    align-items: center;
    overflow: hidden;
  }
  .card::before {
    width: 27rem;
    height: 27rem;
    content: "";
    position: relative;
    top: 0;
    left: -5rem;
    background-image: url("/images/illustration-woman-online-desktop.svg"), url("/images/bg-pattern-desktop.svg");
    background-position: 15px 63px, -385px -235px;
    background-repeat: no-repeat;
    background-size: 25rem, 50rem;
  }
}

.card-images {
  padding-top: 0.625rem;
  padding-bottom: 2.5rem;
  position: relative;
  transition: 0.4s;
  width: 100%;
  display: flex;
}
@media (min-width: 62.5em) {
  .card-images {
    display: none;
  }
}
.card-images::after {
  content: "";
  position: absolute;
  top: -6.75rem;
  left: 15%;
  background-image: url("/images/illustration-woman-online-mobile.svg"), url("/images/bg-pattern-mobile.svg");
  background-position: center top, center 7rem;
  background-repeat: no-repeat;
  width: 15rem;
  height: 15rem;
}
@media (min-width: 62.5em) {
  .card-images::after {
    display: none;
  }
}

h3 {
  text-align: center;
  font-size: 1.875rem;
  font-weight: 700;
  padding-block: 0.625rem;
  text-transform: uppercase;
}
@media (min-width: 62.5em) {
  h3 {
    text-align: left;
  }
}

.accordion {
  padding-inline: 2.1875rem;
  padding-block: 2.8125rem;
  margin-top: 1.875rem;
}
@media (min-width: 62.5em) {
  .accordion {
    width: 21.875rem;
    display: flex;
    flex-direction: column;
    margin-right: 6.25rem;
  }
}

.accordion-button {
  width: 100%;
  text-align: left;
  border: none;
  background-color: transparent;
  color: hsl(var(--dark-blue));
  font-size: 0.875rem;
  cursor: pointer;
  padding-block: 0.625rem;
}
.accordion-button:after {
  content: url("/images/icon-arrow-down.svg");
  float: right;
  margin-left: 0.3125rem;
}
.accordion-button:active, .accordion-button:focus {
  color: hsl(var(--dark-blue));
  font-weight: 700;
}
.accordion-button:hover {
  color: hsl(var(--red));
}

.pannel-wrapper {
  border-bottom: 1px solid hsl(var(--dark-blue)/0.2);
  width: 270px;
}
@media (min-width: 62.5em) {
  .pannel-wrapper {
    width: 320px;
  }
}

.active:after {
  content: url("/images/icon-arrow-down.svg");
  float: right;
  margin-left: 0.3125rem;
  transform: rotate(180deg);
}

.panel {
  display: none;
}
.panel p {
  padding-bottom: 0.625rem;
  color: hsl(var(--dark-blue)/0.7);
}

.panel[aria-expanded=true] {
  max-height: 4rem;
  display: block;
}

.panel[aria-expanded=false] {
  display: none;
}

.panel-text {
  display: none;
}
.panel-text p {
  padding-bottom: 0.625rem;
  color: hsl(var(--dark-blue)/0.7);
}

.panel-text[aria-expanded=true] {
  max-height: 64px;
  display: block;
}

.panel-text[aria-expanded=false] {
  display: none;
}